import "./Contact.css";
import React from 'react';
import IMAGES from "../images/images.js";
function Contact() {
    function infobox(){
      return (
        <div class="informationbox">
          <div class="image-container">
            <img src={IMAGES.logback} alt="Background Image" class="background-image"/>
              <div class="text-overlay">
                <div class="text">
                  <h2>Opening Hours</h2>
                  <p>Mon - Fri: 10AM to 9PM<br></br>
                  Sat - Sun: 11AM to 11PM
                  </p>
                  <h4>Contact Us:</h4>
                  <a href="">9996754545 / 8886754545</a><br></br>
                  <a href="">dds_HYD@instagram.com</a>
                  <h4>Location :</h4>
                  <a href="https://www.google.com/maps/place/MD+Makeovers/@17.5095778,78.4865596,15z/data=!4m6!3m5!1s0x3bcb9b2990b8a875:0xbeeb4d56dcf5dae1!8m2!3d17.5127003!4d78.493557!16s%2Fg%2F11y3lvp5kf?entry=ttu">Navigate</a>
                </div>
              </div>
          </div>
        </div>

      );
    }
    return (
        <div> {infobox()} </div>
    );
}

export default Contact;