import React from "react";
import "./Carousel.css";
import IMAGES from "../images/images.js";

function Main() {
      function createcard(image,text) {
        return (
          <div class="card" style={{backgroundColor:"rgba(179,188,195,255)",}}>
            <a>{image}</a>
            <p class="textincar">{text}
            </p>
          </div>
        );
      }
      function grid() {
        return (
          <div class="grid">
            <div id="box2">OUR SERVICES
            <div class="carousel " id="myCarousel" data-bs-ride="carousel" data-bs-interval="2000">
                <div class="carousel-inner">
                  <div class="carousel-item active">
                    <div>{createcard(<img src={IMAGES.bath} alt="" style={{width: "225px",height: "200px"}}/>,"Bathing & Combing")}</div>
                  </div>
                  <div class="carousel-item">
                    <div>{createcard(<img src={IMAGES.ear} alt="" style={{width: "225px",height: "200px"}}/>, "Ear & Eye Cleaning")}</div>
                  </div>
                  <div class="carousel-item">
                    <div>{createcard(<img src={IMAGES.hair} alt="" style={{width: "225px",height: "200px"}}/>,"Hair Cut & Styling")}</div>
                  </div>
                  <div class="carousel-item">
                    <div>{createcard(<img src={IMAGES.nail} alt="" style={{width: "225px",height: "200px",}}/>, "Nail Clipping")}</div>
                  </div>
                  <div class="carousel-item">
                    <div>{createcard(<img src={IMAGES.paw} alt="" style={{width: "225px",height: "200px",}}/>,"Paw Massage")}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );
      }
      return (
        <div> {grid()} </div>
      );
  }
  
  export default Main;