import React from 'react';
import "./Queries.css";
import IMAGES from "../images/images.js";
function Queries() {
    function querybox(){
      return(
        <div class="twocolbox">
          <div class="col1">
            <h1>Ask Me Anything</h1>
            <p>At Divine Doggy Salon, we pride ourselves on providing top-tier grooming services tailored to pamper your furry friends. With a team of passionate and experienced groomers,  stylish after their grooming session.</p>
            <h3>Visit Us Today</h3>
            <p>Experience the difference at Divine Doggy Salon and treat your dog to the ultimate grooming experience. Whether your dog needs a trim, a bath, or some extra TLC, we're here to make them feel like royalty.</p>
            <p href="">dds@gmail.com</p>
            </div>
          <div class="col2">
            <form>
              <label for="name">Name</label>
              <input type="text" id="name" name="name" required />
              
              <label for="email">Email</label>
              <input type="email" id="email" name="email" required />
          
              <label for="message">Message</label>
              <textarea id="message" name="message" required></textarea>
              
              <button type="submit">Submit</button>
            </form>
          </div>
        </div>

      );
    }
    return (
        <div> {querybox()} </div>
    );
}

export default Queries;